<template>
    <div id="ReceitPage">
        <!-- PRINT START -->
        <vue-easy-print tableShow ref="easyPrint" >
        <div class="box container">
        <article class="media">
            <div class="media-content">

                <div class="level">
                    <h2 class="tag is-primary"><b>Patients Information Sytem - Receit</b></h2>
                </div>

                <div class="level">
                    <div class="level-left">
                    <div class="level-item">
                            <b>Patient name:  </b> 
                            <u> 
                                {{ firstname }} 
                                {{ middlename }} 
                                {{ lastname }} 
                            </u>
                    </div>
                    </div>
                </div>

                <div class="level">
                        
                        <div class="level-item">
                                <b>DOB:  </b> 
                                <u> {{ dateOfBirth }} </u>
                        </div>
                        <div class="level-item"></div>

                        <div class="level-item">
                            <p class="is-pulled-right">
                                <b>Issue date:  </b> 
                                <u> {{ issueDate }} </u>
                            </p>
                        </div>
                </div>

                <div class="level">
                        <div class="level-item">
                            <img src="../assets/rx.jpg" alt="" class="image is-128x128">
                        </div>
                        <div class="level-item">
                                {{ prescription }}
                        </div>
                </div>
                <div class="level">
                    <div class="level-left"></div>
                    <div class="level-right">
                        <div class="level-item">
                            <p style="text-decoration: overline">Doctor's name and Signature</p>
                        </div>
                    </div>
                </div>
            </div>
        </article>
        </div>
        </vue-easy-print>
        <button class="button is-success is-rounded" @click="print">Print</button>

    </div>
</template>
<script>
import db from './firebaseInit'
import vueEasyPrint from 'vue-easy-print'

export default {
    name: 'ReceitPage',
    components: {
        vueEasyPrint,
    },
    data() {
        return {
            userProfileId: this.$route.params.userProfileId,
            userHistoryId: this.$route.params.userHistoryId,

            // data need
            firstname: null,
            middlename: null,
            lastname: null,
            dateOfBirth: null,

            issueDate: null,
            prescription: null
        }
    },
    methods: {
        print () {
            this.$refs.easyPrint.print()
        },
    },
    beforeRouteEnter (to, from, next) {
        // user/patient profile
        db.collection('users').where('uid_user', '==', to.params.userProfileId).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(userProf => {
                    userProf.id = doc.id,
                    userProf.name = doc.data().name,
                    userProf.uid_user = doc.data().uid_user
                })
            })
        })
        // patient history
        db.collection('history').where('usersId', '==', to.params.userHistoryId).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(userHist => {
                    userHist.id = doc.id,
                    userHist.date = doc.data().date
                    userHist.prescription = doc.data().prescription
                })
            })
        })   
    },
    created() {
        // get patient profile == name and dob only
        db.collection('users').where('uid_user', '==', this.$route.params.userProfileId).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                this.firstname = doc.data().firstname,
                this.middlename = doc.data().middlename,
                this.lastname = doc.data().lastname,
                this.dateOfBirth = doc.data().dateOfBirth
            })
        })
        
        // get patient history record == date and prescription
        db.collection('history').doc(this.$route.params.userHistoryId).get().then(doc => {
            this.issueDate = doc.data().date
            this.prescription = doc.data().prescription
        })

    }
}
</script>
<style>
@media print {
    button .button {
        display: none;
    }
    figure, img {
        height: 128px;
        width: 128px;
        display: inherit;
    }

    html {
        margin-left: 10%;
        margin-right: 10%;
    }

}
</style>

