<template>
    <div id="login columns is-centered">
    <vue-particles
        color="#ccffff"
        :particleOpacity="0.7"
        :particlesNumber="70"
        shapeType="circle"
        :particleSize="8"
        linesColor="#ccffff"
        :linesWidth="3"
        :lineLinked="true"
        :lineOpacity="0.4"
        :linesDistance="150"
        :moveSpeed="3"
        :hoverEffect="true"
        hoverMode="grab"
        :clickEffect="true"
        clickMode="push"
      >
    </vue-particles>
        <form @submit.prevent="login" id="form" class="animated fadeInUp">

        <div v-if="isRequired" class="animated shake tag column is-4 is-offset-4 is-danger">Please input required filleds</div>
        <br>

            <div class="field column is-4 is-offset-4">
            <p class="control has-icons-left has-icons-right">
                <input class="input is-rounded is-medium" type="email" placeholder="Email"  v-model="email">
                <span class="icon is-small is-left">
                <i class="fas fa-envelope"></i>
                </span>
            </p>
            </div>
            <div class="field column is-4 is-offset-4">
            <p class="control has-icons-left has-icons-right">
                <input class="input is-rounded is-medium" type="password" placeholder="Password"  v-model="password">
                <span class="icon is-small is-left">
                <i class="fas fa-key"></i>
                </span>
            </p>
            </div>
            <div class="field column is-4 is-offset-4" id="loginbtn">
                <button class="button is-link is-rounded is-medium" v-bind:class="{ 'is-loading': isClick }"  type="submit">Login</button>
            </div>
        </form>

    </div>
</template>
<script>
import firebase from 'firebase/app';
import 'firebase/auth';

export default {
    name: 'login',
    data () {
        return {
            email: null,
            password: null,

            isClick: false,
            isRequired: false
        }
    },
    methods: {
        login (e) {
            e.preventDefault();
            if(this.email != null && this.password != null) {
                this.isClick = true
                firebase.auth().signInWithEmailAndPassword(this.email, this.password)
                    .then(() => {
                        firebase.auth().onAuthStateChanged((user) => {
                            if(user) {
                                // if secretary
                                if(user.uid == 'leeBwCnWn8bolFBMkWYfVRcNoiV2') {
                                    this.$router.go({path: this.$router.path})
                                    // else if doctor
                                } else if(user.uid == 'tBZvsX581AOoPZu0ZeV4XG71FUi2') {
                                    this.$router.go({path: this.$router.replace('/doctor')})
                                } else {
                                    alert('This account is not authorized to view contents in this site')
                                    this.password = ''
                                    firebase.auth().signOut().then(() => this.$router.replace('/'))
                                }
                            }
                        })
                    })
                    .catch(error => {
                        alert(error.message)
                        this.password = ''
                        this.isClick = false
                    })
            } else {
                this.isRequired = true
            }
        }
    }
}
</script>
<style scope>
.field {
    padding: 0px;
}

#loginbtn {
    text-align: center;
}

form #form {
    position: absolute;
    z-index: 1;
}

canvas {
    position: absolute;
    z-index: -1;
}
</style>

