<template>
    <div class="column is-half is-offset-one-quarter page box">
        <!-- provide into the LIST an array you want to search -->
        <!-- whatever you pass in the event-name will be read in this.$on -->
        <!-- KEYS are the strings you want to search -->
        <div class="field">
            <div class="control has-icons-right">
                <VueFuse
                placeholder="Search for users Patient ID"
                event-name="results"
                :list="patients"
                :keys="['usersId']"
                class="input animated fadeIn"
                />
                <span class="icon is-small is-right">
                    <i class="fas fa-search"></i>
                </span>
            </div>
        </div>

        <div v-if="results.length >= 1 ">
            <div v-for="patients in results" :key="patients.usersId">
                <router-link 
                    class="box has-text-centered animated fadeInUp delay-5s" 
                    v-bind:to="{name: 'patientCheckedByDoctorMore', 
                    params: {usersId: patients.usersId, historyId: patients.id }}">
                    <a>  {{ patients.usersId }} - {{ patients.date }} </a>
                </router-link>
                

            </div>
        </div>
        <div v-else class="box">
            <p class="has-text-centered has-text-black is-link">The Patient your looking for has not been check by the doctor yet</p>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'

export default {
  name: 'patientCheckedByDoctor',
  data () {
    return {
      results: [],
      patients: [],
    }
  },
  created () {
    db.collection('history').orderBy('date', 'desc').where('status', '==', true).onSnapshot( querySnapshot => {
        querySnapshot.forEach(doc => {
            const data = {
                'id': doc.id,
                'usersId': doc.data().usersId,
                'date': doc.data().date
            }
            this.patients.push(data)
        })
    })

    this.$on('results', results => {
      this.results = results
    })

  },

}
</script>
