<template>
    <div>
        <div class="box">
        <vue-easy-print tableShow ref="easyPrint" >
        <template>
            <div class="column is-12 is-size-3"> <h1>Patient Data Sheet</h1> </div>
        <article class="media container" v-for="patient in patients" :key="patient.uid_user">

            <div class="media-left animated bounce delay-5s">
                <figure class="image is-128x128">
                    <img v-bind:src="patient.imageURL" alt="test">
                </figure>
            </div>

            <div class="media-content">
                <div class="content">
                    <b class="tag is-primary">User's Profile</b>
                        <div class="columns is-multiline">
                            <div class="column is-4 animated fadeInUP">
                                <b>Patient #: </b> {{ patient.uid_user }}
                            </div>
                            <div class="column is-5">
                                <b>Patient Name: </b> {{ patient.firstname}} {{ patient.middlename }} {{ patient.lastname}}
                            </div>
                            <div class="column is-2">
                                <b>Age: </b> {{ patient.age }}
                            </div>
                            <div class="column is-4">
                                <b>Date of Birth: </b> {{ patient.dateOfBirth }}
                            </div>
                            <div class="column is-5">
                                <b>Address: </b> {{ patient.address }}
                            </div>
                            <div class="column is-2">
                                <b>height: </b> {{ patient.height }}
                            </div>
                            <div class="column is-4">
                                <b>weight: </b> {{ patient.weight }} kg
                            </div>
                            <div class="column is-5">
                                <b>BP: </b> {{ patient.bloodpressure }}
                            </div>
                            <div class="column is-3">
                                <b>Temperature: </b> {{ patient.temperature }} °C
                            </div>
                            <!-- Patient's history record -->
                            <div class="column is-12">
                                <b>Reason for Check up</b> {{ reasonForCheckup }}
                            </div>
                            <div class="column is-12">
                                <b>Prescription: </b> {{ prescription }}
                            </div>

                        </div>
                </div>
            </div>
        </article>
        </template>
        </vue-easy-print> 

            <div class="column is-12">
                <button class="button is-link" @click="print">Print</button>

                <router-link :to="{ name: 'receitPage', params: { userProfileId: $route.params.usersId , userHistoryId: $route.params.historyId }}" class="button is-primary">Receit</router-link>

            </div>

        </div>
    </div>
</template>
<script>
import db from './firebaseInit'
import vueEasyPrint from 'vue-easy-print'

export default {
    name: 'PatientCheckedByDoctorMore',
    components: {
        vueEasyPrint,
    },
    data() {
        return {
            // patient history
            usersId: null,
            date: null,
            isCheck: null,
            prescription: null,
            reasonForCheckup: null,

            // patient profile
            patients: [],

        }
    },
    methods: {
        print () {
            this.$refs.easyPrint.print()
        },
    },
    created() {

        // get patient history
        let patientHistoryRef = db.collection('history').doc(this.$route.params.historyId)
        
        patientHistoryRef.get().then(doc => {
            if(doc.exists) {
                this.prescription = doc.data().prescription
                this.reasonForCheckup = doc.data().reasonForCheckup
            }
        }).catch(error => {
            alert(error)
        })

        // get patient profile
        db.collection('users').where('uid_user', '==', this.$route.params.usersId).onSnapshot( querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'uid_user': doc.data().uid_user,
                    'firstname': doc.data().firstname,
                    'lastname': doc.data().lastname,
                    'middlename': doc.data().middlename,
                    'address': doc.data().address,
                    'age': doc.data().age,
                    'weight': doc.data().weight,
                    'height': doc.data().height,
                    'bloodpressure': doc.data().bloodpressure,
                    'dateOfBirth': doc.data().dateOfBirth,
                    'temperature': doc.data().temperature,
                    'imageURL': doc.data().imageURL
                }
                this.patients.push(data)
            })
        })
    }
}
</script>
<style>
@media print {
    button .button {
        display: none;
    }
    figure, img {
        height: 128px;
        width: 128px;
        display: inherit;
    }
    #doneChecking {
        display: none;
    }
    .box {
        margin: 10px;
    }
}
</style>